$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-events {
  .upRecentEventsContainerInner {
    padding: 15rem 1rem 1rem 1rem;
    background-color: $blue;
    color: $white;
    border-radius: 5px;
    h2 {
      margin-bottom: 4rem;
    }
    .upFeedItem {
      margin-top: 2rem;
      p {
        margin-bottom: 0.25rem;
        a { display: inline-block; }
      }
      .postTime {
        font-style: italic;
      }
      .postReadMore a {
        font-weight: bold;
        color: $yellow;
      }
    }
  }
  #pastEvents {
    .pastEventContainer {
      background-color: #f2f2f2;
      padding: 1rem;
      text-align: center;
      border-radius: 5px;
      margin-bottom: 15px;
      h3 {
        font-size: 1rem;
      }
      .pastEventImageContainer {
        position: relative;
        .pastEventTeaser {
          position: absolute;
          left: 0;
          bottom: 0;
          right: 0;
          padding: 1rem;
          background: $blue;
          color: $white;
        }
      }
      .pastEventDate {
        margin-top: 1rem;
        text-transform: uppercase;
        color: #999;
        font-size: 0.75rem;
        margin-bottom: 0;
      }
    }
  }
}